
*{
    margin:0;
    padding:0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.footernav{
	position: fixed;
	width:100%;
	height: 40px;
	left: 0;
	bottom:0;
}
.footernav ul{
	list-style: none;
	height: 40px;
	width:100%;
}
.footernav ul li{
	float: left;
	width:25%;
	height:40px;
	line-height: 40px;
	text-align: center;
}




































































